/**
 * 
 * @param {*} _x /left|right|center/ or pixel number
 * @param {*} _y /top|bottom|middle/ or pixel number
 * _x, _y Í∞íÏùÑ Ï≤¥ÌÅ¨ÌïòÏó¨ Î¨∏ÏûêÏó¥Ïù∏Í≤ΩÏö∞ Ïú†Ìö®Ìïú Í∞íÏù∏ÏßÄ, Ïú†Ìö®Ìïú Í∞íÏù¥ ÏïÑÎãåÍ≤ΩÏö∞ px Í∞íÏúºÎ°ú Î¶¨ÌÑ¥.
 */
function checkTargetPosXY(_x, _y){
  const xPosReg = /left|right|center/g;
  const yPosReg = /top|bottom|middle/g; 
  const x = (xPosReg.test(_x)) ? _x : parseInt(_x, 10);
  const y = (yPosReg.test(_y)) ? _y : parseInt(_y, 10);
  return {x, y};
}

/**
 *
 * @param {number} len
 * number Í∞íÏùò ÏûêÎ¶øÏàòÎ•º Í∞ñÎäî ÎÇúÏàòÎ•º Î¶¨ÌÑ¥
 */
export function uid(len) {
  len = len || 7;
  return Math.random()
    .toString(35)
    .substr(2, len);
}

/**
 * esw Ï†úÏñ¥Î•º ÏúÑÌïú ÌÅ¥ÎûòÏä§
 * Í∞êÏãú ÎêòÎäî ÏóòÎ¶¨Î®ºÌä∏ Î≥ÑÎ°ú Ìï¥Îãπ ÌÅ¥ÎûòÏä§Í∞Ä ÏÉùÏÑ±Îê®.
 */
class ESW_ITEM {
  constructor(element, root){
    this.id = uid(); // object Ï≤¥ÌÅ¨Î•º ÏúÑÌïú id ÎÇúÏàò
    this.element = element;
    this.element.dataset['eswId'] = this.id; 

    // element Ïùò Í∞úÎ≥Ñ ÏÖãÌåÖ Í∞íÏù¥ ÏûàÎäîÏßÄ Ï≤¥ÌÅ¨ ÌïòÏó¨ Ï†ÄÏû•
    this.datumPointX = (()=>{
      if( element.dataset['eswCheckX'] ) {
        return checkTargetPosXY(element.dataset['eswCheckX'], 0).x;
      } else {
        return root.checkX;
      };
    })();

    // element Ïùò Í∞úÎ≥Ñ ÏÖãÌåÖ Í∞íÏù¥ ÏûàÎäîÏßÄ Ï≤¥ÌÅ¨ ÌïòÏó¨ Ï†ÄÏû•
    this.datumPointY = (()=>{
      if( element.dataset['eswCheckX'] ) {
        return checkTargetPosXY(0, element.dataset['eswCheckY']).y;
      } else {
        return root.checkY;
      };
    })();

    this.activeTimer = root.option.activeDelay; // ÎîúÎ†àÏù¥ÏãúÍ∞Ñ
    this.isIntersecting = false; // ÌôîÎ©¥Ïóê Îì§Ïñ¥ÏôîÎäîÏßÄ Ïú†Î¨¥
    this.timer = null; // ÌÉÄÏù¥Î®∏Í∞Ä ÏßÄÏ†ïÎê®
    this.activeFunction = root.option.active; // ÌôúÏÑ±Ìôî ÎêòÏóàÏùÑÎïå Ïã§ÌñâÎê† Ìï®Ïàò
    this.deActiveFunction = root.option.deActive; // ÎπÑÌôúÏÑ±Ìôî ÎêòÏóàÏùÑÎïå Ïã§ÌñâÎê† Ìï®Ïàò
  }

  /**
   * @param {boolean} value
   * ÌôîÎ©¥ÏïàÏúºÎ°ú Îì§Ïñ¥Ïò§Í±∞ÎÇò ÎÇòÍ∞îÏùÑÎïå Ìï¥Îãπ Í∞íÏùÑ ÏÖãÌåÖ Ìï† Ïàò ÏûàÏùå.
   * Îì§Ïñ¥ÏôîÏùÑ Í≤ΩÏö∞ true
   * ÎÇòÍ∞îÏùÑ Í≤ΩÏö∞ false
   * Ìï¥Îãπ Í∞íÏù¥ Î≥ÄÍ≤ΩÎêòÎ©¥, ÌÉÄÏù¥Î®∏Î•º Ï¥àÍ∏∞Ìôî ÌïòÍ≥† deActiveFunction ÏùÑ Ïã§ÌñâÌï®.
   */
  set setIntersecting(value){
    if( value !== this.isIntersecting ){
      if( this.timer !== null ){
        window.clearTimeout(this.timer);
        this.timer = null;
      }
    }
    if( !value ){
      this.deActiveFunction(this.element);
    }
    this.isIntersecting = value;
  }
  
  // ÌôîÎ©¥ÏùÑ Í∏∞Ï§ÄÏúºÎ°ú element Í∞Ä Î™áÌçºÏÑºÌä∏Ïóê ÏúÑÏπòÌïòÎäîÏßÄ Î¶¨ÌÑ¥Ìï®.
  // ÏóòÎ¶¨Î®ºÌä∏ Í∞úÎ≥Ñ Í∏∞Ï§ÄÍ∞í ÏùÑ ÎåÄÏûÖÌïú Í∞í.
  get getPercentY(){
    const { element, datumPointY } = this;
    const rect = element.getBoundingClientRect();
    const pointY = (()=>{
      if( typeof datumPointY === 'string' ) {
        switch (datumPointY) {
          case 'top':
            return 0;
          case 'bottom':
            return rect.height;
          default:
            return rect.height / 2;
        }
      } else {
        return datumPointY;
      }
    })();
    const winHeight = window.innerHeight;
    const y = rect.y + pointY;
    const percentY = (winHeight - y) / winHeight * 100;
    return percentY;
  }

  // ÌôîÎ©¥ÏùÑ Í∏∞Ï§ÄÏúºÎ°ú element Í∞Ä Î™áÌçºÏÑºÌä∏Ïóê ÏúÑÏπòÌïòÎäîÏßÄ Î¶¨ÌÑ¥Ìï®.
  // ÏóòÎ¶¨Î®ºÌä∏ Í∞úÎ≥Ñ Í∏∞Ï§ÄÍ∞í ÏùÑ ÎåÄÏûÖÌïú Í∞í.
  get getPercentX(){
    const { element, datumPointX } = this;
    const rect = element.getBoundingClientRect();
    const pointX = (()=>{
      if( typeof datumPointX === 'string' ) {
        switch (datumPointX) {
          case 'top':
            return 0;
          case 'bottom':
            return rect.height;
          default:
            return rect.height / 2;
        }
      } else {
        return datumPointX;
      }
    })();
    const winWidth = window.innerWidth;
    const x = rect.x + pointX;
    const percentX = (winWidth - x) / winWidth * 100;
    return percentX;
  }

  // ÌÉÄÏù¥Î®∏Î•º ÌôúÏÑ±Ìôî ÌïòÍ≥†, Ìï®Ïàò Ïã§Ìñâ Ï§ÄÎπÑ ÏÉÅÌÉúÎ°ú Î≥ÄÍ≤Ω.
  // isIntersecting Ïù¥ true Ïù∏ Í≤ΩÏö∞ ÏµúÏ¢Ö activeFunction Î•º Ïã§Ìñâ.
  active(){
    if( this.timer === null ){
      this.timer = window.setTimeout(()=>{
        if( this.isIntersecting ){
          this.activeFunction(this.element);
        }
      }, this.activeTimer);
    }
  }

  // isIntersecting Í∞íÍ≥º Î¨¥Í¥ÄÌïòÍ≤å Í∞ïÏ†úÎ°ú Ìï®Ïàò Ïã§ÌñâÏùÑ Ï¥àÍ∏∞ÌïòÍ≥†,
  // deActiveFunction ÏùÑ Ïã§ÌñâÌï®.
  deActive(){
    if( this.timer !== null ){
      window.clearTimeout(this.timer);
      this.timer = null;
    }
    this.deActiveFunction(this.element);
  }
}

/**
 * 
 * @param {*} elements 
 * string, nodeList, element Î•º Ï≤¥ÌÅ¨ÌïòÏó¨, [element] Î°ú Î¶¨ÌÑ¥Ìï®.
 */
function elementsArray(elements){
  if( typeof elements === 'string' ){
    return Array.from(document.querySelectorAll(elements));
  } else {
    if( elements.length ) {
      return Array.from(elements);
    } else {
      if( elements ) return [elements]
    }
  }
}



class ESW {
  constructor(elements, set){
    // set
    const option = set;
    option.root = set.root === null ? window : set.root
    const items = elementsArray(elements);
    const checkItems = [];
    const eswObject = {};
    const XY = checkTargetPosXY(set.checkX, set.checkY);
    const checkY = XY.y;
    const checkX = XY.x;
    const io = new IntersectionObserver(entries=>{ // IO Î•º ÏßÄÏ†ï.
      entries.forEach(entry => {
        const target = entry.target;
        const item = this.getEswObj(target);
        item.esw.setIntersecting = entry.isIntersecting; // esw item Ïùò intersecting Í∞íÏùÑ ÏÖãÌåÖ ÌïúÎã§.

        // ÌôîÎ©¥Ïóê Îì§Ïñ¥Ïò§Îäî element Îäî checkItems Ïóê ÎÑ£Í≥†,
        // ÌôîÎ©¥ÏóêÏÑú ÎÇòÍ∞Ñ element Îäî checkItems ÏóêÏÑú Ï†úÍ±∞Ìï®.
        // Ïä§ÌÅ¨Î°§Ïãú loop Í∞Ä Í≥ÑÏÜç ÎèåÍ≤å ÎêòÍ∏∞ ÎïåÎ¨∏Ïóê ÏÑ±Îä•ÏùÑ Í≥†Î†§ÌïòÏó¨...
        if( entry.isIntersecting ){
          this.checkItems.push(target);
        } else {
          const index = this.checkItems.indexOf( target );
          if( index >= 0 ){
            this.checkItems.splice(index, 1);
          }
        }
      });
    });
    
    Object.assign(this, {
      option,
      items,
      io,
      checkItems,
      checkY,
      checkX,
      eswObject,
      isInit : false
    });
    option.init && this.init();
  }

  /**
   * 
   * @param {element} element 
   * element Ïùò dataset.eswId Í∞íÏùÑ Ï≤¥ÌÅ¨ÌïòÏó¨ object Î•º Î¶¨ÌÑ¥Ìï®.
   * ÏóÜÏùÑ Í≤ΩÏö∞Ïùò ÏàòÎäî ÏóÜÎã§....
   */
  getEswObj(element){
    const id = element.dataset['eswId'];
    const esw = this.eswObject[id];
    return {element, id, esw};
  }

  // Ïä§ÌÅ¨Î°§Ïãú Ïã§ÌñâÌï®.
  mot(){
    const { checkItems , option } = this;
    checkItems.forEach(element => {
      const item = this.getEswObj(element);
      const itemYPercent = item.esw.getPercentY;
      
      if( option.activePercentY < itemYPercent ){
        if(option.deActivePercentY < itemYPercent ) {
          item.esw.deActive();
        } else {
          item.esw.active();
        }
      } else {
        item.esw.deActive();
      }
    });
  }

  init(){
    const { items, io, option } = this;
    if( !this.isInit ) {
      option.root.addEventListener('scroll', ()=>{
        this.mot();
      });
    }

    items.forEach(element => {
      if( !element.dataset['eswInit'] ){
        const esw = new ESW_ITEM(element, this);
        this.eswObject[esw.id] = esw;
        io.observe(element, option.threshold);
        element.dataset['eswInit'] = 'init'
      }
    })
    this.isInit = true;
    window.addEventListener('load', ()=>{
      this.mot();  
    });
    this.mot();
  }

  // ÎèôÏ†ÅÏúºÎ°ú element Í∞Ä ÏÉùÏÑ± Îêú Í≤ΩÏö∞ ÏÇ¨Ïö©.
  update(elements){
    const addItems = elementsArray(elements);
    this.items = this.items.concat(addItems);
    this.init();
  }
  disable(){
    
  }
  enable(){

  }
  destroyed(){
    
  }
}

const defaultSetting = {
  root: null, // scroll event bind element
  activePercentY: 60, // ÏßÑÏûÖ Ï≤¥ÌÅ¨ ÏãúÏûë Ìè¨Ïù∏Ìä∏ 
  deActivePercentY : 90, // ÏßÑÏûÖ Ï≤¥ÌÅ¨ ÏóîÎìú Ìè¨Ïù∏Îìú
  activePercentX: 0,
  deActivePercentX : 100,
  activeDelay: 1000, // ÏßÑÏûÖÏãú Ìï¥Îãπ ÏãúÍ∞Ñ ÌõÑ Ìï®Ïàò Ïã§ÌñâÎê®.
  threshold: 0.1, // intersectionObserve Ïùò threshold 
  active: null, // ÏßÑÏûÖÌñàÏùÑÎïå Ïã§ÌñâÎê† callback 
  deActive: null, // ÎÇòÍ∞îÏùÑÎïå Ïã§ÌñâÎê† callback
  init: true, // ÏµúÏ¥à init ÏùÑ Ìï†ÏßÄ ÏòµÏÖò
  checkY: 'top', // top, middle, bottom, custom number(px) , target Ïùò Í∏∞Ï§ÄÏ†ê.
  checkX: 'left' // left, center, right, custom number(px)
}

function ElementScrollWatcher(elements, setting={}){
  const set = Object.assign({}, defaultSetting, setting);
  return new ESW(elements, set);
}

export default ElementScrollWatcher;